import React, { Component } from 'react';
import { FaHiking,  FaShuttleVan, FaBeer, FaCocktail } from "react-icons/fa";
import Title from './Title';

export default class Services extends Component {
    state={
        servives:[
            {
                icon: <FaCocktail/>,
                title: "Free Cocktails",
                info:"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard"
            },
            {
                icon: <FaHiking/>,
                title: "Endless Hiking",
                info:"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard"
            },
            {
                icon: <FaShuttleVan/>,
                title: "Free Shuttle",
                info:"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard"
            },
            {
                icon: <FaBeer/>,
                title: "Strongest Beer",
                info:"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard"
            }
        ]
    }
    render() {
        return (
            <section className="services">
                <Title title="services"></Title>
                <div className="services-center">
                    {this.state.servives.map((item, index) => {
                        return <article key={index} className="service">
                                    <span>{item.icon}</span>
                                    <h6>{item.title}</h6>
                                    <p>{item.info}</p>
                        </article>
                    })}
                </div>
            </section>
        );
    }
}
